body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #f8f8f8;
  background: #dfdfdf;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  text-align: center;
  cursor: pointer;
  background: linear-gradient(270deg, #d3c747, #db5634);
  border: none;
  border-radius: 4px;
  align-items: center;
  margin: 0 auto;
  padding: 10px 20px;
  font-weight: bold;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), border-color .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 2px 4px rgba(0, 0, 0, .2);
}

button:hover, button:focus {
  background-color: #74797a;
  box-shadow: 0 8px 15px rgba(0, 0, 0, .2);
}

.wrapper-button {
  text-align: center;
  margin-bottom: 30px;
}

.search-button {
  margin: 0;
}

.search-form {
  width: 100%;
  background-image: linear-gradient(to right, rgba(210, 203, 197, .88), rgba(201, 189, 178, .88));
  justify-content: center;
  padding: 20px;
  display: flex;
}

.search-input {
  width: 30%;
  color: #404243;
  border: none;
  border-radius: 5px;
  outline: none;
  margin-right: 15px;
  padding: 5px;
  font-size: 17px;
  box-shadow: 0 8px 15px rgba(0, 0, 0, .2);
}

.gallery {
  grid-gap: 20px;
  max-width: calc(100vw - 80px);
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 30px auto;
  padding: 0;
  display: grid;
}

.photo-card {
  background: linear-gradient(270deg, rgba(210, 203, 197, .88), rgba(201, 189, 178, .88));
  border: 1px solid #000;
  border-radius: 10px;
  transition: all 1s;
  overflow: hidden;
  box-shadow: 0 0 20px rgba(243, 243, 243, .6);
}

.photo-card:hover, .photo-card:focus {
  transform: scale(1.02);
  box-shadow: 0 10px 13px -7px #000, 4px 1px 14px 5px rgba(0, 0, 0, 0);
}

.gallary-image {
  transition: transform .5s linear;
}

.gallary-image:hover {
  transform: scale(1.05);
}

.info {
  color: #2d3131;
  flex-wrap: nowrap;
  align-content: center;
  justify-content: center;
  padding: 10px 0;
  display: flex;
}

.info-item {
  text-align: center;
}

.info-item:not(:last-child) {
  margin-right: 15px;
}

.gallary-image {
  width: 100%;
  height: 250px;
  object-fit: cover;
  display: block;
}

